import * as THREE from 'three'
import CANNON from 'cannon'

export default class CannonHelper {
    constructor(scene) {
        this.scene = scene;
    }

    addLights(renderer) {

        // LIGHTS
        const ambient = new THREE.AmbientLight(0x888888, 1);
        this.scene.add(ambient);

        const light = new THREE.DirectionalLight(0xdddddd, 1);
        light.position.set(3, 10, 4);
        this.scene.add(light);
    }

    set shadowTarget(obj) {
        if (this.sun !== undefined) this.sun.target = obj;
    }

    createCannonTrimesh(geometry) {
        if (!geometry.isBufferGeometry) return null;

        const posAttr = geometry.attributes.position;
        const vertices = geometry.attributes.position.array;
        let indices = [];
        for (let i = 0; i < posAttr.count; i++) {
            indices.push(i);
        }

        return new CANNON.Trimesh(vertices, indices);
    }

    createCannonConvex(geometry) {
        if (!geometry.isBufferGeometry) return null;

        const posAttr = geometry.attributes.position;
        const floats = geometry.attributes.position.array;
        const vertices = [];
        const faces = [];
        let face = [];
        let index = 0;
        for (let i = 0; i < posAttr.count; i += 3) {
            vertices.push(new CANNON.Vec3(floats[i], floats[i + 1], floats[i + 2]));
            face.push(index++);
            if (face.length == 3) {
                faces.push(face);
                face = [];
            }
        }

        return new CANNON.ConvexPolyhedron(vertices, faces);
    }

    addVisual(body, name, currentMaterial, type, castShadow = false, receiveShadow = false) {
        body.name = name;
        this.currentMaterial = currentMaterial
        if (this.currentMaterial === undefined) this.currentMaterial = new THREE.MeshLambertMaterial({ color: 0xC0C0C0, side: THREE.DoubleSide, wireframe: true });
        if (this.settings === undefined) {
            this.settings = {
                stepFrequency: 60,
                quatNormalizeSkip: 2,
                quatNormalizeFast: true,
                gx: 0,
                gy: 0,
                gz: 0,
                iterations: 3,
                tolerance: 0.0001,
                k: 1e6,
                d: 3,
                scene: 0,
                paused: false,
                rendermode: "solid",
                constraints: false,
                contacts: false,  // Contact points
                cm2contact: false, // center of mass to contact points
                normals: false, // contact normals
                axes: false, // "local" frame axes
                particleSize: 0.1,
                shadows: false,
                aabbs: false,
                profiling: false,
                maxSubSteps: 3
            }
            this.particleGeo = new THREE.SphereGeometry(1, 16, 8);
            this.particleMaterial = new THREE.MeshLambertMaterial({ color: 0xff0000 });
        }
        // What geometry should be used?
        if (type === 'special') {

            let mesh;
            if (body instanceof CANNON.Body) mesh = this.shape2model(body, castShadow, receiveShadow, name);

            if (mesh) {
                // Add body
                body.threemesh = mesh;
                mesh.castShadow = castShadow;
                mesh.receiveShadow = receiveShadow;
                this.scene.add(mesh);
            }
        }
        else if (type === undefined || type === 'basic') {

            let mesh;
            if (body instanceof CANNON.Body) mesh = this.shape2Mesh(body, castShadow, receiveShadow, name);

            if (mesh) {
                // Add body
                body.threemesh = mesh;
                mesh.castShadow = castShadow;
                mesh.receiveShadow = receiveShadow;


                world.addEventListener('postStep', () => {

                    mesh.position.copy(body.position);
                    mesh.quaternion.copy(body.quaternion);
                    mesh.translateOnAxis(new THREE.Vector3(0, 0, 1), 0.6);
                });
                this.scene.add(mesh);
            }
        }
    }

    shape2model(body, castShadow, receiveShadow, name) {
        const obj = new THREE.Object3D();
        obj.name = name
        const material = this.currentMaterial;
        let geometry, mesh = new THREE.Object3D();
        let index = 0
        if (name == 'car') {
            mesh = assetData['car-chassis'].scene;
            mesh.scale.set(3, 3, 2.5);

            var q = body.shapeOrientations[index++];
            mesh.position.set(0, 0, 0);
            mesh.quaternion.set(q.x, q.y, q.z, q.w);
            mesh.castShadow = castShadow
            mesh.receiveShadow = receiveShadow

            obj.add(mesh);
            return obj;
        }
        else if (name === 'floor') {
            geometry = new THREE.PlaneGeometry(10, 10, 4, 4);
            mesh = new THREE.Object3D();
            var submesh = new THREE.Object3D();
            var ground = new THREE.Mesh(geometry, this.currentMaterial);
            ground.scale.set(100, 100, 100);
            submesh.add(ground);
            ground.receiveShadow = true
            ground.castShadow = true
            submesh.castShadow = true
            mesh.receiveShadow = true

            mesh.add(submesh);
            var o = body.shapeOffsets[0];
            var q = body.shapeOrientations[0];
            mesh.position.set(o.x, o.y, o.z);
            mesh.quaternion.set(q.x, q.y, q.z, q.w);

            obj.add(mesh);
            return obj
        }
        else if (name.slice(0, 5) == 'wheel') {
            let a = true;
            if (a) {
                let number = name.slice(5, 6)

                let newModel = assetData['car-wheel'].scene.clone()
                newModel.scale.set(2.8, 2.8, 2.8);
                var o = body.shapeOffsets[index];
                var q = body.shapeOrientations[index++];
                newModel.position.set(o.x, o.y, o.z);
                newModel.quaternion.set(q.x, q.y, q.z, q.w);
                if (number == '2' || number == '0') {
                    newModel.rotateX(Math.PI)
                }
                obj.add(newModel);
                return obj;
            }
        }
    }



    shape2Mesh(body, castShadow, receiveShadow, name) {
        const obj = new THREE.Object3D();
        obj.name = name
        const material = this.currentMaterial;
        const game = this;
        let index = 0;
        let model = false

        if (name.slice(0, 6) == 'letter') {

            let mesha
            mesha = letters[`${name.slice(6, 7)}`].clone()
            mesha.scale.set(5, 5, 5);
            mesha.position.set(-0.6, -1.7, 0.8)
            obj.add(mesha);
            // return obj
        }
        // else {

        body.shapes.forEach(function (shape) {
            let mesh;
            let geometry;
            let v0, v1, v2;
            switch (shape.type) {

                case CANNON.Shape.types.SPHERE:
                    const sphere_geometry = new THREE.SphereGeometry(shape.radius, 8, 8);
                    mesh = new THREE.Mesh(sphere_geometry, material);
                    break;

                case CANNON.Shape.types.PARTICLE:
                    mesh = new THREE.Mesh(game.particleGeo, game.particleMaterial);
                    const s = this.settings;
                    mesh.scale.set(s.particleSize, s.particleSize, s.particleSize);
                    break;

                case CANNON.Shape.types.PLANE:
                    geometry = new THREE.PlaneGeometry(10, 10, 4, 4);
                    geometry.computeBoundingBox();

                    var bbox = geometry.boundingBox;
                    var size = new THREE.Vector3().subVectors(bbox.max, bbox.min); // we'll use it to get normalized positions of vertices in faces of geometryetry

                    var vertexIndices = ['a', 'b', 'c'];
                    var face, vertex, normalized = new THREE.Vector3(), normalizedY = 0;

                    var red = new THREE.Color("#f5883c"), blue = new THREE.Color("#fccf92");

                    for (var i = 0; i < geometry.faces.length; i++) {
                        face = geometry.faces[i];
                        for (var v = 0; v < 3; v++) {
                            vertex = geometry.vertices[face[vertexIndices[v]]];
                            normalizedY = normalized.subVectors(vertex, bbox.min).divide(size).y; // we'll use the normalized Y-coordinate
                            face.vertexColors[v] = red.clone().lerp(blue, normalizedY);
                        }
                    }
                    mesh = new THREE.Object3D();
                    const submesh = new THREE.Object3D();
                    const ground = new THREE.Mesh(geometry, material);
                    ground.position.set(0, 0, 0)
                    ground.scale.set(100, 100, 1);
                    submesh.add(ground);

                    mesh.add(submesh);
                    break;

                case CANNON.Shape.types.BOX:

                    const box_geometry = new THREE.BoxGeometry(shape.halfExtents.x * 2,
                        shape.halfExtents.y * 2,
                        shape.halfExtents.z * 2);
                    mesh = new THREE.Mesh(box_geometry, material);
                    break;

                case CANNON.Shape.types.CYLINDER:
                    console.log('here')
                    break;

                case CANNON.Shape.types.CONVEXPOLYHEDRON:
                    // console.log(shape)
                    // let geomet = new THREE.CylinderGeometry(0.5,1.5,1,20,20)
                    // mesh = new THREE.Mesh(geomet, material)
                    // break;

                    const geo = new THREE.Geometry();

                    // Add vertices
                    shape.vertices.forEach(function (v) {
                        geo.vertices.push(new THREE.Vector3(v.x, v.y, v.z));
                    });

                    shape.faces.forEach(function (face) {
                        // add triangles
                        const a = face[0];
                        for (let j = 1; j < face.length - 1; j++) {
                            const b = face[j];
                            const c = face[j + 1];
                            geo.faces.push(new THREE.Face3(a, b, c));
                        }
                    });
                    geo.computeBoundingSphere();
                    geo.computeFaceNormals();
                    mesh = new THREE.Mesh(geo, material);
                    break;

                case CANNON.Shape.types.HEIGHTFIELD:
                    geometry = new THREE.Geometry();

                    v0 = new CANNON.Vec3();
                    v1 = new CANNON.Vec3();
                    v2 = new CANNON.Vec3();
                    for (let xi = 0; xi < shape.data.length - 1; xi++) {
                        for (let yi = 0; yi < shape.data[xi].length - 1; yi++) {
                            for (let k = 0; k < 2; k++) {
                                shape.getConvexTrianglePillar(xi, yi, k === 0);
                                v0.copy(shape.pillarConvex.vertices[0]);
                                v1.copy(shape.pillarConvex.vertices[1]);
                                v2.copy(shape.pillarConvex.vertices[2]);
                                v0.vadd(shape.pillarOffset, v0);
                                v1.vadd(shape.pillarOffset, v1);
                                v2.vadd(shape.pillarOffset, v2);
                                geometry.vertices.push(
                                    new THREE.Vector3(v0.x, v0.y, v0.z),
                                    new THREE.Vector3(v1.x, v1.y, v1.z),
                                    new THREE.Vector3(v2.x, v2.y, v2.z)
                                );
                                var i = geometry.vertices.length - 3;
                                geometry.faces.push(new THREE.Face3(i, i + 1, i + 2));
                            }
                        }
                    }
                    geometry.computeBoundingSphere();
                    geometry.computeFaceNormals();
                    mesh = new THREE.Mesh(geometry, material);
                    break;

                case CANNON.Shape.types.TRIMESH:

                    if (name.slice(0, 6) == 'letter') {
                        model = true
                        loader.load('assets/alphabets/.gltf', (gltf) => {
                            gltf.scene.scale.set(5, 5, 5);
                            mesh = gltf.scene
                            mesh.receiveShadow = receiveShadow;
                            mesh.castShadow = castShadow;


                            geometry = new THREE.Geometry();

                            v0 = new CANNON.Vec3();
                            v1 = new CANNON.Vec3();
                            v2 = new CANNON.Vec3();
                            for (let i = 0; i < shape.indices.length / 3; i++) {
                                shape.getTriangleVertices(i, v0, v1, v2);
                                geometry.vertices.push(
                                    new THREE.Vector3(v0.x, v0.y, v0.z),
                                    new THREE.Vector3(v1.x, v1.y, v1.z),
                                    new THREE.Vector3(v2.x, v2.y, v2.z)
                                );
                                var j = geometry.vertices.length - 3;
                                geometry.faces.push(new THREE.Face3(j, j + 1, j + 2));
                            }
                            geometry.computeBoundingSphere();
                            geometry.computeFaceNormals();
                            let meshb = new THREE.Mesh(geometry, material);


                            obj.add(mesh);
                            obj.add(meshb)
                            return obj;

                        }, (xhr) => {
                            console.log((xhr.loaded / xhr.total * 100) + '% loaded');
                        }, (err) => {
                            console.log(err)
                        })
                    }
                    else {
                        geometry = new THREE.Geometry();

                        v0 = new CANNON.Vec3();
                        v1 = new CANNON.Vec3();
                        v2 = new CANNON.Vec3();
                        for (let i = 0; i < shape.indices.length / 3; i++) {
                            shape.getTriangleVertices(i, v0, v1, v2);
                            geometry.vertices.push(
                                new THREE.Vector3(v0.x, v0.y, v0.z),
                                new THREE.Vector3(v1.x, v1.y, v1.z),
                                new THREE.Vector3(v2.x, v2.y, v2.z)
                            );
                            var j = geometry.vertices.length - 3;
                            geometry.faces.push(new THREE.Face3(j, j + 1, j + 2));
                        }
                        geometry.computeBoundingSphere();
                        geometry.computeFaceNormals();
                        mesh = new THREE.Mesh(geometry, material);
                    }



                    break;

                default:
                    throw "Visual type not recognized: " + shape.type;
            }
            if (!model) {
                mesh.receiveShadow = receiveShadow;
                mesh.castShadow = castShadow;

                mesh.traverse(function (child) {
                    if (child.isMesh) {
                        child.castShadow = castShadow;
                        child.receiveShadow = receiveShadow;
                    }
                });

                var o = body.shapeOffsets[index];
                var q = body.shapeOrientations[index++];
                mesh.position.set(o.x, o.y, o.z);
                mesh.quaternion.set(q.x, q.y, q.z, q.w);

                obj.add(mesh);
            }
        });

        return obj;

        // }
    }

    updateBodies(world) {
        world.bodies.forEach(function (body) {
            if (body.threemesh != undefined) {
                body.threemesh.position.copy(body.position);
                body.threemesh.quaternion.copy(body.quaternion);
            }
        });
    }
}